/* PAGE LOADER */
@-webkit-keyframes rotate-right {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }
@keyframes rotate-right {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }

@-webkit-keyframes rotate-left {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(-360deg); } }

@keyframes rotate-left {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(-360deg); } }

.page-loader {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99999;
  background: rgba(59, 66, 86, 0.95); }

.page-loader_container {
  position: absolute;
  width: 100%;
  left: 0;
  top: 50%;
  margin-top: -58px; }

.page-loader_spin {
  position: relative;
  margin: 0 auto 20px;
  width: 64px;
  height: 70px;
  background-image: url(data:image/svg+xml;base64,); }

.page-loader_spin:before {
  -webkit-animation-name: rotate-right;
          animation-name: rotate-right;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 26px;
  height: 26px;
  margin-top: -13px;
  margin-left: -13px;
  background-image: url(data:image/svg+xml;base64,); }

.page-loader_spin-item {
  -webkit-animation-name: rotate-left;
          animation-name: rotate-left;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  margin-left: -8px;
  margin-top: -8px;
  background-image: url(data:image/svg+xml;base64,); }

.page-loader_spin:after {
  -webkit-animation-name: rotate-right;
          animation-name: rotate-right;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  display: block;
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 8px;
  margin-left: -4px;
  margin-top: -4px;
  background-image: url(data:image/svg+xml;base64,); }

.page-loader_label {
  text-align: center;
  color: #C7CCD1;
  margin-bottom: 0;
  margin-top: 0; }

/* /PAGE LOADER */
